import React from "react";
const isMobile = typeof window !== 'undefined' && window.innerWidth  < 783;

const LiveList = ({ data: { allMarkdownRemark: { edges } } }) => {
    return (
            <a className="row liveDiv is-dark-blue" href={edges[0].node.frontmatter.ticketLink} >
                <div className={`liveDate ${isMobile ? "col-xs-4": "col-lg-2"}`}>
                    <h2>{edges[0].node.frontmatter.day}</h2>
                    <h4>{edges[0].node.frontmatter.month}</h4>
                </div>
                <div className={`liveInfo ${isMobile ? "col-xs-8": "col-lg-10"}`}>
                    <div className="div-ception">
                        <h4>{edges[0].node.frontmatter.venue}, {edges[0].node.frontmatter.desc}</h4>
                        <h3>{edges[0].node.frontmatter.city}</h3>
                    </div>
                </div>
            </a>
    )
}

export const query = graphql`
   {
     allMarkdownRemark {
       edges {
         node {
           frontmatter {
             venue
             desc
             day
             city
             month
             ticketLink
           }
         }
       }
     }
   }
 `

 export default LiveList;