import React from "react"
import imageURLs from "../data/images-cds";
import HomeImage from "../components/home/HomeImage";
import Layout from "../components/layout"
import SEO from "../components/seo"
import Joie from "../components/joie"
import LiveList from "../components/LiveList"
import Footer from "../components/footer";
import ScrollableAnchor from 'react-scrollable-anchor';
const isMobile = typeof window !== 'undefined' && window.innerWidth  < 480;
const bgImage = isMobile ? "Mobile" : "Desktop";


const IndexPage = ({ data }) => (
  <Layout>
    <SEO title="SHAI BRIDES" />
    <HomeImage />
    <div className={`logoHomeDiv${bgImage}`}>
            <img src={imageURLs.Logos.white} className={`logoHome${bgImage}`} />
            <a href='#live' >
              <img src={imageURLs.Icons["Down-arrow"]} className={`downIcon${bgImage}`} />
            </a>
        </div>
    <ScrollableAnchor id={'live'}>
      <div className="emptyDiv" />
    </ScrollableAnchor>
    <div className="is-mint-bg landingLiveDiv">
      <h1>UPCOMING SHOWS</h1>
      <LiveList data={data}/>
    </div>
    <Joie />
    <Footer bgColour="dark-blue"/>
  </Layout>
)

export const query = graphql`
   {
     allMarkdownRemark {
       edges {
         node {
           frontmatter {
             venue
             desc
             day
             city
             month
             ticketLink
           }
         }
       }
     }
   }
 `
 

export default IndexPage;